import React from "react"

import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Image from "react-bootstrap/Image";

import content from "./content";

import './style.scss'

export default () => {
	return (
		<div className={"py-5 my-5"}>
			{content.map((service, idx) => {
				return (
					<Row className="my-5 justify-content-center">
						<Col lg={{span: 6, order: idx%2}} md={{span:10, order:2}} xs={{span:11, order:2}}>
							<div className="d-flex flex-column justify-content-center h-100">
								<h4 class="text-dark-blue">{service.title}</h4>
								<p className={"text-justify font-weight-light"}>
									{service.description}
								</p>
								
								<Row>
									{service.items.map(item => (
										<Col xs={11} sm={11} md={6} as={"h6"} className={"my-3"}>{item}</Col>
									))}
								</Row>
							</div>
						</Col>
						<Col lg={6} md={10} sm={12} xs={12} className={`justify-content-center ${idx%2 === 1 ? 'reverse': null}`}>
							<img src={service.image} className={`service-image`} />
						</Col>
					</Row>
				)
			})}
		</div>
	)
}
